<template>
  <section class="container">
    <article class="article" id="article">
      内训讲师列表
    </article>
  </section>
</template>
<script>
  import {mapState, mapActions} from 'vuex'
  import merge from 'webpack-merge'

  export default {
    data() {
      return {
        navIndex: 0,
      }
    },
    computed: {
      ...mapState({
        list: state => state.article.articleList,
        pagination: state => state.article.pagination
      }),
    },
    created() {
      document.title = `讲师 - 插座学院`
    },
    methods: {
      /**
       * 路由跳转
       * @param path 路由地址
       */
      toPath(path) {
        this.$router.push(path)
      },
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .container {
    width: 75%;
    margin: 0 auto;
    overflow: hidden;
    min-height: 100vh;
    background-color: #FFFFFF;
    margin-bottom: -295px;
    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 380px;
    }
  }

  .article {
    flex: 9;
    background: #fff;
    & .article-nav {
      padding: 16px 0;
      border-bottom: 1px solid #efefef;
    }

    & .article-nav-box {
      display: flex;
      align-items: center;
    }

    & .article-nav-item {
      cursor: pointer;
      padding: 0 24px;
      position: relative;
      font-size: 18px;
      color: #404040;

      &:after {
        display: block;
        content: "";
        width: 1px;
        height: 16px;
        position: absolute;
        top: 5px;
        right: 0;
        border-right: 1px solid #efefef;
      }

      &:last-child:after {
        display: none;
      }

      &:hover {
        color: #409EFF;
      }
    }

    & .article-nav-item--active {
      color: #409EFF;
    }

    & .article-item {
      cursor: pointer;
      padding: 24px;
      display: flex;
      border-bottom: 1px solid #f4f4f4;

      &:hover {
        background: #f4f4f4;
      }

      &:hover .article-title {
        color: #409EFF;
      }
    }

    & .article-img {
      .article-img-inner {
        width: 96px;

        & img {
          width: 100%;
          border-radius: 5px;
        }
      }
    }


    .article-content {
      flex: 1;
      margin-right: 24px;
    }

    & .article-title {
      color: #464c5b;
      font-size: 24px;
      margin: 0;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    & .article-info {
      & p {
        display: inline-block;
        margin-right: 24px;
        margin-top: 24px;
        font-size: 16px;
        color: #9ea7b4;
      }

      & p.article-category {
        height: 32px;
        line-height: 32px;
        padding: 0 32px;
        font-size: 16px;
        color: #409EFF;
        border-radius: 32px;
        background: rgba(51, 119, 255, .1);
      }
    }
  }

  .article-empty {
    text-align: center;
    padding: 24px 0;
    font-size: 16px;
    color: #989898;
  }

  .page {
    padding: 32px 0;
    text-align: center;
  }

  @media screen and (min-width: 200px) and (max-width: 768px) {
    .container {
      box-sizing: border-box;
      display: flex;
      width: 100%;
      overflow: hidden;
      flex-direction: column;
    }

    .sidebar {
      box-sizing: border-box;
      width: auto;
      flex: 1;
      margin-left: 0;
      margin-top: 24px;
    }
  }
</style>



